const state = {
  rowIndex: null,
  cellIndex: null,
  visibl: false,
  rmBtn: false,
  top: 0,
  left: 0
}

const mutations = {
  setRow (state, rows) {
    state.rowIndex = rows
  },
  setCell (state, cell) {
    state.cellIndex = cell
  },
  visibility (state, visibl) {
    state.visibl = visibl
  },
  hided (state, visibl) {
    state.visibl = visibl
  },
  rmBtnEnter (state, hovered) {
    state.rmBtn = hovered
  },
  rmBtnLeave (state, hovered) {
    state.rmBtn = hovered
  },
  setTop (state, top) {
    state.top = top + 'px'
  },
  setLeft (state, left) {
    state.left = left + 'px'
  }
}

const actions = {
  addRow ({ commit }) {
    let row = state.rowIndex + 1
    commit('setRow', row)
  },
  addCell ({ commit }) {
    let cell = state.cellIndex + 1
    commit('setCell', cell)
  },
  rmRow ({ commit }) {},
  rmCell ({ commit }) {},
  showBtn ({ commit }) {
    let visibl = true
    commit('visibility', visibl)
  },
  hideBtn ({ commit }) {
    let visibl = false
    commit('hided', visibl)
  },
  rmBtnEnter ({ commit }) {
    commit('rmBtnEnter', true)
  },
  rmBtnLeave ({ commit }) {
    commit('rmBtnLeave', false)
  },
  setTop ({ commit }, { top, left }) {
    commit('setTop', top)
    commit('setLeft', left)
  }
}

const getters = {
  getRows () {
    return state.rowIndex
  },
  getCells () {
    return state.cellIndex
  },
  getVisibility () {
    return state.visibl
  },
  getRmBtnHover () {
    return state.rmBtn
  },
  getTop () {
    return state.top
  },
  getLeft () {
    return state.left
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}



