<template>
  <td @mouseover="getRectElement()"></td>
</template>

<script>
import store from '../../store/index'
import { mapActions } from 'vuex'
export default {
  name: 'cell',
  data () {
    return {
      model: {
        top: '',
        left: ''
      }
    }
  },
  methods: {
    ...mapActions(['setTop']),
    getRectElement () {
      let top = this.$el.parentElement.offsetTop + 50
      let left = this.$el.offsetLeft + 50
      console.log(this.$el)
      store.dispatch('setTop', { top, left })
      /*
      store.dispatch('setRectElement')
      this.$el.parentElement.offsetTop
      console.log(this.$el)
      console.log(this.$el.parentElement)
      */
    }
  }
}
</script>

<style scoped>
td {
  width: 50px;
  height: 50px;
  background-color: var(--table-color);
}
</style>



