<template>
  <section>
    <div class="container">
      <table
        class="container__table"
        @mouseover="showBtn()"
        @mouseout="hideBtn()">
        <row />
      </table>
      <button
        class="btn__add btn__add-row"
        @click.prevent="addCell">+</button>
    </div>
    <button
      class="btn__add btn__add-col"
      @click.prevent="addRow">+</button>
    <button
      class="btn__rm btn__rm-row"
      @click.prevent="delRow"
      v-show="visibility || rmBtnHovered"
      @mouseover="btnHover()"
      @mouseout="btnLeave()"
      v-bind:style="{ top: top }">-</button>
    <button
      class="btn__rm btn__rm-col"
      @click.prevent="delCell"
      v-show="visibility || rmBtnHovered"
      @mouseover="btnHover()"
      @mouseout="btnLeave()"
      v-bind:style="{ left: left }">-</button>
  </section>
</template>

<script>
import _ from 'lodash'
import row from './row'
import store from '../../store/index'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'grid',
  components: {
    row
  },
  data () {
    return {}
  },
  methods: {
    ...mapActions([
      'addRow', 'addCell', 'rmRow', 'rmCell'
    ]),
    addRow (context) {
      store.dispatch('addRow')
    },
    addCell () {
      store.dispatch('addCell')
    },
    delRow () {
      console.log('del row')
    },
    delCell () {
      console.log('del cell')
    },
    showBtn: _.debounce(() => {
      store.dispatch('showBtn')
    }, 1000),
    hideBtn: _.debounce(() => {
      store.dispatch('hideBtn')
    }, 1000),
    btnHover () {
      store.dispatch('rmBtnEnter')
    },
    btnLeave () {
      store.dispatch('rmBtnLeave')
    }
  },
  computed: {
    ...mapGetters([
      'getVisibility', 'getRmBtnHover', 'getTop', 'getLeft'
    ]),
    visibility () {
      return store.getters.getVisibility
    },
    rmBtnHovered () {
      return store.getters.getRmBtnHover
    },
    top () {
      return store.getters.getTop
    },
    left () {
      return store.getters.getLeft
    }
  },
  beforeCreate: function () {
    let getDefaultCell = localStorage.getItem('defaultCell')
    let getDefaultRow = localStorage.getItem('defaultRow')
    if (!getDefaultCell && !getDefaultRow) {
      localStorage.setItem('defaultRow', 4)
      localStorage.setItem('defaultCell', 4)
      store.state.grid.rowIndex = 4
      store.state.grid.cellIndex = 4
    } else {
      store.state.grid.rowIndex = parseInt(getDefaultRow)
      store.state.grid.cellIndex = parseInt(getDefaultCell)
    }
  },
  created: function () {},
  mounted: function () {}
}
</script>

<style scoped>
.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 55px 0 0 55px;
}
.container__table {
  border: 1px solid var(--table-color);
  margin: 0 3px 3px 0px;
  border-spacing: 2px;
}
.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 55px 0 0 55px;
}
.container__table {
  border: 1px solid var(--table-color);
  margin: 0 3px 3px 0px;
  border-spacing: 2px;
}
.btn__add:hover,
.btn__rm:hover {
  cursor: pointer;
  visibility: visible;
  opacity: 1;
  transition-delay: 0s;
}
.btn__add,
.btn__rm {
  border: 0;
  color: var(--white);
  width: 50px;
  height: 50px;
  display: block;
  text-align: center;
  font-weight: bold;
  line-height: 2.5;
  font-size: 18px;
}
.btn__rm {
  animation: rmBtnLostHover 0.5s;
  animation-fill-mode: forwards;
  margin: 3px;
}
.btn__rm:hover {
  animation: rmBtnHover 0.5s;
  animation-fill-mode: forwards;
  display: block;
}
.btn__add {
  animation: addBtnlostHover 0.5s;
  animation-fill-mode: forwards;
}
.btn__add:hover {
  animation: addBtnHover 0.5s;
  animation-fill-mode: forwards;
}
.btn__rm-row,
.btn__rm-col {
  position: absolute;
  top: 0;
}
.btn__add-row {
  margin: 3px 0 0 0;
}
.btn__add-col {
  margin: 0 0 0 58px;
}
@keyframes addBtnHover {
  from {
    background-color: var(--btn-add-color);
  }
  to {
    background-color: var(--btn-add-hover);
  }
}
@keyframes addBtnlostHover {
  from {
    background-color: var(--btn-add-hover);
  }
  to {
    background-color: var(--btn-add-color);
  }
}
@keyframes rmBtnHover {
  from {
    background-color: var(--btn-rm-color);
  }
  to {
    background-color: var(--btn-rm-hover);
  }
}
@keyframes rmBtnLostHover {
  from {
    background-color: var(--btn-rm-hover);
  }
  to {
    background-color: var(--btn-rm-color);
  }
}
</style>



