<template>
  <tbody>
    <tr
      v-for="n in row"
      :key="n">
      <cell
        v-for="x in cell"
        :key="x" />
    </tr>
  </tbody>
</template>

<script>
import cell from './cell.vue'
import store from '../../store/index'
import { mapGetters } from 'vuex'

export default {
  components: {
    cell
  },
  beforeCreate: function () {},
  beforeUpdate: function () {
    localStorage.setItem('defaultRow', store.state.grid.rowIndex)
    localStorage.setItem('defaultCell', store.state.grid.cellIndex)
    console.log('beforeUpdate')
  },
  name: 'row',
  methods: {

  },
  computed: {
    ...mapGetters([
      'getCells', 'getRows'
    ]),
    cell () {
      return store.getters.getCells
    },
    row () {
      return store.getters.getRows
    }
  }
}
</script>

<style scoped>

</style>



